@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');

.osjs-gui {
    font-weight: 300;
    display: flex;
    position: relative;
    min-width: 1em;
    min-height: 1em;
    box-sizing: border-box
}

.osjs-window-content>.osjs-gui-box:only-child {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0
}

.osjs-gui-horizontal {
    flex-direction: column
}

.osjs-gui-vertical {
    flex-direction: row
}

.osjs-icon {
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-color: transparent;
    background-size: 100%;
    min-width: 1rem;
    min-height: 1rem;
    display: inline-block;
    vertical-align: middle
}

.osjs-gui-box {
    display: flex;
    overflow: hidden
}

.osjs-gui-box>.osjs-gui-menubar,
.osjs-gui-box>.osjs-gui-statusbar,
.osjs-gui-box>.osjs-gui-toolbar {
    flex-shrink: 0
}

.osjs-gui-box-container,
.osjs-gui-box>.osjs-gui {
    margin: .5em;
    flex-shrink: 0
}

.osjs-gui-button>button {
    cursor: pointer;
    display: block;
    margin: 0;
    padding: .5em;
    outline: 0 none;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border: 0;
    background: transparent;
    box-sizing: border-box;
    font-family: inherit;
    font-size: inherit;
    white-space: nowrap
}

.osjs-gui-button>button::-moz-focus-inner {
    border: 0;
    padding: 0
}

.osjs-gui-button>button>.osjs-icon+.osjs-label {
    margin-left: .5em
}

.osjs-gui-button>button>span {
    vertical-align: middle
}

.osjs-gui-field {
    position: relative
}

.osjs-gui-field input,
.osjs-gui-field select,
.osjs-gui-field textarea {
    display: block;
    width: 100%;
    height: 100%;
    outline: none;
    padding: .5em;
    margin: 0;
    background: transparent;
    border: 0;
    box-sizing: border-box;
    resize: none;
    font-family: inherit;
    font-size: inherit;
    border-radius: 0
}

.osjs-gui-textarea-field {
    flex-direction: column
}

.osjs-gui-textarea-field>textarea {
    flex-grow: 1
}

.osjs-gui-select-field>div {
    position: relative;
    flex-basis: 100%
}

.osjs-gui-select-field>div>select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none
}

.osjs-gui-select-field>div:after {
    content: "↴";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: inline-block;
    width: 1em;
    height: 1em;
    right: .5em
}

.osjs-gui-toggle-field>label {
    position: relative;
    user-select: none !important;
    display: flex
}

.osjs-gui-toggle-field>label>input {
    display: block;
    position: absolute;
    visibility: hidden;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none
}

.osjs-gui-toggle-field>label>input:checked+span:after {
    display: block
}

.osjs-gui-toggle-field>label>.osjs-toggle-input {
    position: relative;
    display: inline-block;
    width: 1em;
    height: 1em
}

.osjs-gui-toggle-field>label>.osjs-toggle-input:after,
.osjs-gui-toggle-field>label>.osjs-toggle-input:before {
    content: "";
    position: absolute;
    width: 1em;
    height: 1em;
    box-sizing: border-box
}

.osjs-gui-toggle-field>label>.osjs-toggle-input:before {
    z-index: 10
}

.osjs-gui-toggle-field>label>.osjs-toggle-input:after {
    content: "❌";
    display: none;
    font-size: 80%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 20
}

.osjs-gui-toggle-field>label>.osjs-toggle-label {
    display: block;
    flex-grow: 1;
    position: relative;
    padding-left: .5em
}

.osjs-gui-field-label {
    display: flex;
    flex-wrap: nowrap;
    justify-content: left
}

.osjs-gui-field-label>label {
    display: block
}

.osjs-gui-field-label>.osjs-gui {
    margin: 0
}

.osjs-gui-field-label.osjs-gui-field-label-on-top {
    flex-direction: column
}

.osjs-gui-field-label.osjs-gui-field-label-on-top>label {
    margin-bottom: .5em
}

.osjs-gui-field-label.osjs-gui-field-label-on-bottom {
    flex-direction: column-reverse
}

.osjs-gui-field-label.osjs-gui-field-label-on-bottom>label {
    margin-top: .5em
}

.osjs-gui-field-label.osjs-gui-field-label-on-left {
    flex-direction: row;
    align-items: center
}

.osjs-gui-field-label.osjs-gui-field-label-on-left>label {
    margin-right: .5em
}

.osjs-gui-field-label.osjs-gui-field-label-on-left>.osjs-gui {
    flex: 1 1
}

.osjs-root[data-dir=rtl] .osjs-gui-select-field>div:after {
    right: auto;
    left: .5em
}

.osjs-gui-list-view {
    overflow: hidden
}

.osjs-gui-list-view .osjs-icon {
    margin-right: .5em
}

.osjs-gui-list-view-wrapper {
    min-height: 4em;
    display: flex;
    flex-basis: 100%;
    overflow: auto;
    align-items: flex-start;
    position: relative
}

.osjs-gui-list-view-pane {
    min-width: 1em;
    min-height: 1em;
    position: relative;
    flex: 1 1
}

.osjs-gui-list-view-pane:last-child {
    flex-grow: 1
}

.osjs-gui-list-view-cell,
.osjs-gui-list-view-header {
    position: relative;
    padding: .5em;
    flex-shrink: 0;
    z-index: 1;
    min-height: 1em;
    display: flex
}

.osjs-gui-list-view-cell>span,
.osjs-gui-list-view-header>span {
    min-height: 1rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    flex: 1 1;
    line-height: 1
}

.osjs-gui-list-view-header {
    z-index: 10;
    position: sticky;
    top: 0
}

.osjs-root[data-dir=rtl] .osjs-gui-list-view .osjs-icon {
    margin-right: 0;
    margin-left: .5em
}

.osjs-gui-icon-view-wrapper {
    flex-basis: 100%;
    flex-wrap: wrap;
    overflow: auto
}

.osjs-gui-icon-view-entry {
    position: relative;
    display: inline-block;
    width: 6em;
    height: 7em;
    text-align: center;
    overflow: visible;
    z-index: 1
}

.osjs-gui-icon-view-entry.osjs__active {
    z-index: 2
}

.osjs-gui-icon-view-entry.osjs__active .osjs__label {
    overflow: visible !important;
    height: auto !important
}

.osjs-gui-icon-view-entry * {
    pointer-events: none
}

.osjs-gui-icon-view-entry>.osjs__container {
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%
}

.osjs-gui-icon-view-entry>.osjs__container>.osjs__image {
    padding: .5em
}

.osjs-gui-icon-view-entry>.osjs__container>.osjs__image>.osjs-icon {
    width: 3em;
    height: 3em
}

.osjs-gui-icon-view-entry>.osjs__container>.osjs__label {
    padding: .5em;
    height: 2em;
    line-height: 1.2;
    overflow: hidden;
    word-break: break-all
}

.osjs-gui-icon-view-entry>.osjs__container>.osjs__label>span,
.osjs-gui-image {
    display: block
}

.osjs-gui-video {
    display: block;
    background-color: #000;
    flex-basis: 100%
}

.osjs-gui-video>video {
    background-color: #000;
    width: 100%;
    height: 100%
}

.osjs-gui-menubar {
    margin: 0 !important
}

.osjs-gui-menubar>div {
    box-sizing: border-box
}

.osjs-gui-menubar>div>span {
    margin: .5em;
    display: block;
    pointer-events: none
}

.osjs-gui-menu {
    position: absolute;
    z-index: 9999999999;
    left: 20px !important;
    top: 84px !important;
    font-family:"Raleway";
    animation: revealMenu 0.4s ease-in-out forwards;
}



@keyframes revealMenu {
    0% {
        opacity: 0;
        transform: translateY(-100%);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}


.osjs-gui-menu ul {
    list-style-type: none;
    padding: 1rem;
}

.osjs-gui-menu ul,
.osjs-gui-menu ul>li {
    margin: 0;
    padding: 1rem;
}

.osjs-gui-menu ul>li>div {
    position: relative;
    cursor: pointer
}

.osjs-gui-menu ul>li>div>ul {
    display: none;
    position: absolute;
    z-index: 10;
    left: 100%;
    top: 0
}

.osjs-gui-menu ul>li>div>ul.clamp-right {
    left: auto;
    right: 100%
}

.osjs-gui-menu ul>li>div:hover>ul {
    display: block
}

.osjs-gui-menu-separator {
    height: 1px;
    display: block;
    padding: .5em;
    box-sizing: border-box
}

.osjs-gui-menu-separator:after {
    content: "";
    display: block;
    background: hsla(0, 0%, 100%, .5);
    height: 1px
}

.osjs-gui-menu-label {
    font-family:"Raleway" !important;
    font-weight:bold !important;
    display: flex;
    padding: .5em 1.5em .5em .5em;
    box-sizing: border-box;
    position: relative;
    pointer-events: none;
    white-space: nowrap;
    min-height: 1rem;
    align-items: center
}

.osjs-gui-menu-label>.osjs-gui-menu-checkbox,
.osjs-gui-menu-label>.osjs-icon {
    margin-right: .5em
}

.osjs-gui-menu-label>.osjs-gui-menu-checkbox:after {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    content: "✓";
    text-align: center;
    display: none
}

.osjs-gui-menu-label>.osjs-gui-menu-checkbox.active:after {
    display: inline
}

.osjs-gui-menu-container[data-has-children]>.osjs-gui-menu-label:after {
    content: "▶";
    position: absolute;
    right: .5em;
    top: 50%;
    transform: translateY(-50%)
}

.osjs-root[data-dir=rtl] .osjs-gui-menu-label {
    padding-right: .5em;
    padding-left: 1.5em
}

.osjs-root[data-dir=rtl] .osjs-gui-menu-label>.osjs-gui-menu-checkbox,
.osjs-root[data-dir=rtl] .osjs-gui-menu-label>.osjs-icon {
    margin-right: 0;
    margin-left: .5em
}

.osjs-root[data-dir=rtl] .osjs-gui-menu-container[data-has-children]>.osjs-gui-menu-label:after {
    right: auto;
    left: .5em
}

.osjs-gui-panes {
    height: 100%;
    overflow: hidden;
    position: relative
}

.osjs-gui-panes-inner {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex
}

.osjs-gui-panes-inner.osjs-gui-vertical>.osjs-gui-panes-pane>.osjs-gui {
    width: 100%
}

.osjs-gui-panes-inner.osjs-gui-vertical>.osjs-gui-panes-spacer {
    height: 100%;
    width: .5em;
    min-width: .5em;
    cursor: ew-resize
}

.osjs-gui-panes-inner.osjs-gui-horizontal>.osjs-gui-panes-pane>.osjs-gui {
    height: 100%
}

.osjs-gui-panes-inner.osjs-gui-horizontal>.osjs-gui-panes-spacer {
    width: 100%;
    height: .5em;
    min-height: .5em;
    cursor: ns-resize
}

.osjs-gui-panes-pane {
    position: relative;
    margin: .5em;
    box-sizing: border-box;
    overflow: hidden;
    display: flex
}

.osjs-gui-panes-pane:last-of-type {
    flex-grow: 1
}

.osjs-gui-progressbar-wrapper {
    display: flex;
    z-index: 1;
    width: 100%;
    line-height: 2;
    padding: .5em;
    text-align: center;
    min-height: 2em;
    position: relative;
    box-sizing: border-box;
    flex-grow: 1
}

.osjs-gui-progressbar-value {
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    z-index: 2
}

.osjs-gui-progressbar-label {
    z-index: 3;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%
}

.osjs-gui-progressbar-label>span {
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)
}

.osjs-gui-statusbar {
    white-space: nowrap
}

.osjs-gui-tabs {
    display: flex;
    flex: 1 1
}

.osjs-gui-tabs-wrapper {
    display: flex;
    flex-grow: 1;
    flex-direction: column
}

.osjs-gui-tabs-header {
    display: flex;
    flex-shrink: 0
}

.osjs-gui-tabs-header>div {
    padding: .5em;
    cursor: pointer;
    position: relative;
    z-index: 2;
    white-space: nowrap
}

.osjs-gui-tabs-panes {
    position: relative;
    z-index: 1;
    flex-grow: 1;
    display: flex;
    overflow: hidden
}

.osjs-gui-tabs-panes>div {
    display: none;
    padding: .5em;
    width: 100%;
    overflow: auto
}

.osjs-gui-tabs-panes>div.osjs__active {
    display: flex;
    flex-grow: 1;
    flex-shrink: 1
}

.osjs-gui-toolbar {
    display: flex
}

.osjs-gui-toolbar>.osjs-gui {
    flex-shrink: 0
}

.osjs-gui-toolbar.osjs-gui-vertical>.osjs-gui:not(:last-child) {
    margin-right: 1em
}

.osjs-gui-toolbar.osjs-gui-horizontal>.osjs-gui:not(:last-child) {
    margin-bottom: 1em
}

.osjs-root[data-dir=rtl] .osjs-gui-toolbar.osjs-gui-vertical>.osjs-gui:not(:last-child) {
    margin-right: 0;
    margin-left: 1em
}

.osjs-root[data-dir=rtl] .osjs-gui-toolbar.osjs-gui-horizontal>.osjs-gui:not(:last-child) {
    margin-bottom: 0;
    margin-top: 1em
}

.osjs-gui-iframe>iframe {
    display: block;
    border: 0;
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%
}

.osjs-gui-expander {
    display: block
}

.osjs-gui-expander-wrapper {
    display: flex;
    flex-direction: column
}

.osjs-gui-expander-header {
    flex: 1 1;
    display: flex;
    padding: .5em;
    cursor: pointer
}

.osjs-gui-expander-header-icon {
    flex: 0 0;
    min-width: 1em;
    margin-right: .5em
}

.osjs-gui-expander-header-icon:after {
    content: "▼"
}

.osjs-gui-expander-header-icon[data-active=true]:after {
    content: "▲"
}

.osjs-gui-expander-header-label {
    flex: 1 1
}

.osjs-gui-expander-content {
    margin-top: 1em
}